@import url(https://fonts.googleapis.com/css?family=Cabin+Sketch&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-page {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 80vh;
}

h1 {
    font-family: 'Kaushan Script', cursive, Georgia, 'Times New Roman', Times, serif; 
    color:black;
}

.button-wrapper {
    display: flex;
    justify-content: center;
}

.button-wrapper button {
    margin: 0px 10px;
}

.create-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 50%;
    height: 50%;
    background-color: white;
    padding: 10px;
    text-align: center;
}

#create-room-button {
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    display: block;
    margin-top: 5%;
}
h2.h3 {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

.cards-button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    align-content: flex-end;
}

.cards {
    font-size: 50px;
    font-family: 'Cabin Sketch', cursive, Verdana, Geneva, Tahoma, sans-serif; 
    width: 125px;
    height: 155px;
    margin-right: 12px;
    margin-bottom: 12px;
    border: gray;
    position: relative;
    cursor: pointer;
    color: #1e2a36;
    box-shadow: 2px 4px 8px -1px hsla(0,0%,51%,.7);
    -webkit-transition-duration: .5s;
            transition-duration: .5s;
}

.cards:hover {
    font-size: 4em;
}

.function {
    cursor: pointer;

}

.show-btn.MuiButtonBase-root {
    margin: 0px 10px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

